'use strict';

// MODULES //

import isAccessorArray from '@stdlib/array-base-assert-is-accessor-array';
import accessorGetter from '@stdlib/array-base-accessor-getter';
import getter from '@stdlib/array-base-getter';
import dtype from '@stdlib/array-dtype';


// MAIN //

/**
* Returns an accessor function for retrieving an element from an array-like object.
*
* @param {Collection} x - input array
* @returns {Function} accessor
*
* @example
* var arr = [ 1, 2, 3, 4 ];
*
* var get = resolveGetter( arr );
* var v = get( arr, 2 );
* // returns 3
*/
function resolveGetter( x ) {
	var dt = dtype( x );
	if ( isAccessorArray( x ) ) {
		return accessorGetter( dt );
	}
	return getter( dt );
}


// EXPORTS //

export default resolveGetter;
